jQuery(document).ready(function () {
	var baotredate = moment.utc().format();
	var baotretoday = moment().format('[] dddd');
	var baotrelocal = moment.utc(baotredate).local().format("MMM, Do YYYY - hh:mm A");
	jQuery("#tretoptime").html(baotretoday + " - " + baotrelocal)
});
(function ($) {
	'use strict';
})(jQuery);